<template>
  <v-container fill-height fluid>
    <div class="header">
      <h3 class="headline secondary--text" v-if="title">{{title}}</h3>
      <h4 class="caption white--text" v-if="titleSub">{{titleSub}}</h4>
    </div>
    <div class="contents">
      <!--<div class="v-form">
        <div class="testA">aa</div>
        <div class="testB">bb</div>
        <v-btn class="testC" color="accent" depressed @click="reset"></v-btn>
      </div>-->
      <v-form ref="form" v-model="valid" lazy-validation>
        <div class="v-input-group">
          <!--<v-text-field
            v-model="email"
            :rules="emailRules"
            label="E-mail"
            required
            append-icon="close"
          ></v-text-field>-->

          <v-text-field v-model="contact" label="" required append-icon="close"></v-text-field>
          <v-text-field v-model="company" label="" required append-icon="close"></v-text-field>

          <!--<v-select
            v-model="select"
            :items="items"
            :rules="[v => !!v || 'Item is required']"
            label=""
            required
          ></v-select>-->
        </div>

        <v-textarea
          class="textarea"
          name="comment"
          label=""
          placeholder
          hide-details
          no-resize
          height="100%"
        ></v-textarea>
        <!--<div class="v-textarea">
          <div class="v-input__control">aa</div>
        </div>-->

        <v-btn color="accent" depressed @click="reset"></v-btn>
      </v-form>
    </div>
  </v-container>
</template>
<script>
// import store from "../store";
export default {
  data () {
    return {
      // inHeight: this.$store.state.inHeight,
      valid: true,
      title: '',
      titleSub: '   ',
      article: '',
      contact: '',
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
        v => (v && v.length <= 10) || 'Name must be less than 10 characters'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      company: '',
      select: null,
      items: [
        ' ',
        ' ',
        ' ',
        'A Report',
        'C Builder',
        'X Studio'
      ],
      checkbox: false
    }
  },
  mounted () {
    // console.log(this.$store.state.isMobile)
  },
  methods: {
    validate () {
      if (this.$refs.form.validate()) {
        this.snackbar = true
      }
    },
    reset () {
      this.$refs.form.reset()
    }
    /* resetValidation() {
      this.$refs.form.resetValidation();
    } */
  }
}
</script>

<style lang="scss">
.v-form {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  width: 100%;
  height: 100%;
  .v-btn {
    min-width: 140px;
  }
}
.v-input-group {
  .v-icon {
    color: rgba(255, 255, 255, 0.54) !important;
  }
}
.v-label {
  color: #03a9f4 !important;
}
.v-input:not(.v-input--is-disabled) input,
.v-input:not(.v-input--is-disabled) textarea {
  color: rgba(255, 255, 255, 0.87);
}

.v-input:not(.v-input--is-disabled) input,
.v-input:not(.v-input--is-disabled) textarea,
.v-select .v-select__selections {
  color: #fff !important;
}

.v-text-field > .v-input__control > .v-input__slot > .v-text-field__slot {
  flex-direction: column;
}
.v-textarea {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  .v-input__control {
    border: 0px solid #f0f;
    flex: 1 1 auto;
    height: 100%;
    min-height: 100%;
    .v-input__slot {
      padding: 0 !important;
      align-items: self-start;
      flex-direction: row;
      display: flex;
      height: 100%;
      .v-text-field__slot {
        border: 0px solid #0f0;
        height: 100%;
        margin: 0 !important;
        .v-label {
          min-height: 32px;
          top: 0;
          display: flex;
          align-items: center;
          transform: translateY(0px) scale(1);
          position: relative !important;
        }
        textarea {
          border: 0px solid #0277bd;
        }
        textarea::placeholder {
          color: red !important;
          font-family: "Noto Sans Light", sans-serif !important;
        }
      }
    }
  }
}

/*@media (max-width: 750px) {
  ul {
    margin-top: 0;
  }
}*/
</style>
