<template>
  <v-container fill-height fluid>
    <div class="header">
      <h3 class="headline" v-if="title">{{title}}</h3>
      <h4 class="caption white--text" v-if="titleSub">{{titleSub}}</h4>
    </div>
    <div class="contents">
      <ul class="information" v-if="list">
        <li v-for="item in list" :key="item.idx">{{item.contents}}</li>
      </ul>
      <div class="mapGroup">
        <div class="map" ref="mapContainer"></div>
        <!--<vue-daum-map
          class="map"
          :appKey="appKey"
          :center.sync="center"
          :level.sync="level"
          :mapTypeId="mapTypeId"
          :libraries="libraries"
          @load="onLoad"
          @center_changed="onMapEvent('center_changed', $event)"
          @zoom_start="onMapEvent('zoom_start', $event)"
          @zoom_changed="onMapEvent('zoom_changed', $event)"
          @bounds_changed="onMapEvent('bounds_changed', $event)"
          @click="onMapEvent('click', $event)"
          @dblclick="onMapEvent('dblclick', $event)"
          @rightclick="onMapEvent('rightclick', $event)"
          @mousemove="onMapEvent('mousemove', $event)"
          @dragstart="onMapEvent('dragstart', $event)"
          @drag="onMapEvent('drag', $event)"
          @dragend="onMapEvent('dragend', $event)"
          @idle="onMapEvent('idle', $event)"
          @tilesloaded="onMapEvent('tilesloaded', $event)"
          @maptypeid_changed="onMapEvent('maptypeid_changed', $event)"
          style
        ></vue-daum-map>-->
      </div>
    </div>
  </v-container>
</template>

<script>
import loadScriptOnce from 'load-script-once'
import config from '../config'
export default {
  // components: { VueDaumMap },
  data () {
    return {
      // inHeight: this.$store.state.inHeight,
      title: '  ',
      titleSub: '       ',
      article: '',
      list: [
        {
          title: 'information',
          contents: 'A:   54 30-9  IT B607'
        },
        { title: 'tel', contents: 'T:055 761 1070' }
      ],

      map: null,
      appKey: config.appKey,
      libraries: [],
      center: { lat: 35.166899, lng: 128.131954 },
      level: 5
    }
  },
  mounted () {
    loadScriptOnce(
      `//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=${
        this.appKey
      }&libraries=${this.libraries.join(',')}`
    )
      .then(() => {
        daum.maps.load(() => {
          this.render()
          this.bindEvents()
          this.$emit('load', this.map)
        })
      })
      .catch(err => {
        console.error(err)
      })
  },
  methods: {
    /* "ROADMAP": 1,
    "NORMAL": 1,
    "SKYVIEW": 2,
    "HYBRID": 3,
    "OVERLAY": 4,
    "ROADVIEW": 5,
    "TRAFFIC": 6,
    "TERRAIN": 7,
    "BICYCLE": 8,
    "BICYCLE_HYBRID": 9,
    "USE_DISTRICT": 10 */
    init () {},
    render () {
      const currentTypeId = ''
      const mapPosition = new kakao.maps.LatLng(
        this.center.lat,
        this.center.lng
      )
      const markers = [
        {
          position: mapPosition
        },
        {
          position: mapPosition,
          text: '()' // text         
        }
      ]
      const options = {
        center: mapPosition,
        level: this.level,
        marker: markers
        // mapTypeId: kakao.maps.MapTypeId.TRAFFIC
        // draggable: false
      }
      // this.map = new kakao.maps.StaticMap(this.$refs["mapContainer"], options);

      this.map = new kakao.maps.Map(this.$refs['mapContainer'], options)
      // this.map.addOverlayMapTypeId(kakao.maps.MapTypeId.TERRAIN);

      const imageSrc = require('../assets/images/marker_red.png')
      const imageSize = new kakao.maps.Size(64, 69)
      const imageOption = { offset: new kakao.maps.Point(27, 69) }

      const markerImage = new kakao.maps.MarkerImage(
        imageSrc,
        imageSize,
        imageOption
      )

      var marker = new kakao.maps.Marker({
        position: mapPosition,
        image: markerImage
      })

      marker.setMap(this.map)

      // this.map.removeOverlayMapTypeId(currentTypeId);
    },
    bindEvents () {},
    bindEvent () {}
  }
}
</script>
<style lang="scss" scoped>
ul {
  display: flex;
  flex-wrap: wrap;
  margin-top: 24px;
  li {
    margin: 0;
    padding: 0;
    list-style: none;
    /*font-family: "Noto Sans Medium";*/
    color: #fff !important;
    padding-bottom: 16px;
    padding-right: 16px;

    &::before {
      content: "+";
      padding-right: 8px;
      font-family: "Noto Sans DemiLight";
      color: rgba(0, 0, 0, 0.7);
    }
  }
}
.headline {
  color: #fff;
}
.information {
  font-family: "Noto Sans DemiLight", sans-serif !important;
  color: #fff;
  flex: 0 1 auto;
}
.mapGroup {
  flex: 1 1 auto;
  width: 100%;
  background-color: #f2f0e8;
  .map {
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 750px) {
  ul {
    margin-top: 0;
    li {
      &:first-child {
        padding-bottom: 0px;
      }
      &::before {
        content: "";
        padding-right: 0px;
      }
    }
  }
}
</style>
