
<template>
  <keep-alive>
    <swiper :options="swiperOption">
      <swiper-slide>
        <router-view name="detail"></router-view>
      </swiper-slide>
      <div class="swiper-scrollbar" slot="scrollbar"></div>
    </swiper>
  </keep-alive>
</template>

<script>
export default {
  props: {},
  data () {
    return {
      swiperOption: {
        direction: 'vertical',
        slidesPerView: 'auto',
        freeMode: true,
        mousewheel: true,
        scrollbar: {
          el: '.swiper-scrollbar',
          draggable: true,
          hide: true
        },
        keyboard: {
          enabled: true,
          onlyInViewport: false
        }
      }
    }
  },
  created () {},
  mounted () {}
}
</script>
<style lang="scss">
.application.detail {
  .v-content {
    .v-content__wrap {
      height: 100%;
      .swiper-container {
        width: 100%;
        height: 100%;
        .swiper-wrapper {
          .swiper-slide {
            height: auto;
            .container {
              max-width: 750px;
            }
          }
        }
      }
    }
  }
}
</style>
